.container {
  margin: 0 auto;
  width: 1170px;
  @include max-screen(1199px) {width: 970px;}
  @include max-screen(991px) {width: 750px;}
  @include max-screen(767px) {width: 610px;}
}
.noscroll {}
.table-wrapper {overflow: auto;}
a.lightgallery {border-bottom: none;}
ul.slick-dots li button, .slick-slide, .slick-arrow {outline: none;}





/******************************************************/
.wrapper {
	color: #293133;
	text-shadow: 1px 1px 1px rgba(0,0,0,0.2);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;

}
.container {
  position: relative;
  padding: 5px 20px;
}


.sites__list {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    padding-left: 10px;
}
.sites__list_2 {
    display: flex;
    flex-flow: row wrap;
}

.sites {
    margin-top: 100px;
}

.sites__list a {
    font-size: 15px;
    font-weight: 400;
    color: #101010;
    text-decoration: none;
}
.sites__changes,
.contacts,
.sites__done,
.helpers {
  margin-bottom: 15px;
}
.photo {
    display: flex;
    justify-content: center;
    align-items: center;
}


.frontend {
 font-family: 'Colus', arial;
}
.hello {
  padding: 0 5px;
}
.ya-share2 {
    margin-right: 20px;
}
.ya-share2__list_direction_horizontal > .ya-share2__item {
  margin: 0 5px 5px 0 !important;
}


@media screen and (min-width: 992px) {
  .container {
    width: 1170px;
    margin: 20px auto;
  }
}
@media screen and (max-width: 991px) and (min-width: 769px) {
  .container {
    min-width: 700px;
    width: 100%;
    margin: 15px auto;
  }
}
@media screen and (max-width: 768px) {
  h1 {
    font-size: 28px;
  }
  body {
    padding: 5px 10px;
  }
  .container {
    min-width: 300px;
    width: 100%;
    margin: 5px auto;
    padding: 15px;
  }
  .sites__list_2 a {
    width: 140px;
    margin: 5px auto;
    font-size: 14px;
  }
  .description {
    display: block;
    margin: 5px;
  }
  .cont1 {
	flex-flow: row wrap;
	.vk, .skype {
		padding-bottom: 20px;
		margin: 0;
	}
  }

}


.helpers {
	display: flex;
	justify-content: center;
	div {
		margin: 10px 30px;
	}
}

.position {
    font-size: 18px;
}
